.main-info {
  width: 393px;
  padding-left: 14px;

  @media (max-width: $tablet-width) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 340px;
    width: 100%;
    background-color: $grey;
    background-image: url("../img/header-bike@1x.jpg");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    padding: 0;
    margin-bottom: 63px;

    @media (min-resolution: 144dpi), (min-resolution: 2dppx) {
      background-image: url("../img/header-bike@2x.jpg");
      background-repeat: no-repeat;
      background-position: center center;
      background-size: cover;
    }
  }

  @media (max-width: $mobile-width-only) {
    min-height: 390px;
  }
}

.main-info h1 {
  margin: 0;
  font-weight: 700;
  font-size: 46px;
  line-height: 64px;
  margin-top: 25px;
  margin-bottom: 22px;
  letter-spacing: -0.005em;

  @media (max-width: $tablet-width) {
    width: auto;
    font-size: 30px;
    line-height: 42px;
    text-align: center;
    margin-top: 55px;
    margin-bottom: 20px;
  }

  @media (max-width: $mobile-width-only) {
    width: 270px;
    margin-top: 48px;
  }
}

.main-info p {
  margin: 0;
  font-weight: 300;
  font-size: 20px;
  line-height: 32px;
  opacity: 0.8;

  @media (max-width: $tablet-width) {
    max-width: 435px;
    font-size: 18px;
    line-height: 32px;
    text-align: center;
  }

  @media (max-width: $mobile-width-only) {
    max-width: 288px;
  }
}
